<template>
    <div class="black-bg" v-if="modalStatus == true">
        <div class="white-bg">
            <h4>상세페이지</h4>
            <img class="room-img" v-bind:src="roomsInfo[currentProductNo].image">
            <h4>{{ roomsInfo[currentProductNo].title }}</h4>
            <p>{{ roomsInfo[currentProductNo].content }}</p>
            <!-- <input @input="month = $event.target.value" type="text"> -->
            <input v-model.number="month" type="text">
            <p>  {{ month }} 개월 선택함 : {{ roomsInfo[currentProductNo].price * month}}원</p>
            <span>이 물건의 허위매물신고 건수 : {{ 신고수[currentProductNo] }}</span>
            <button @click="$emit('초기화', currentProductNo)">신고건수 초기화</button>
            <Discount v-if="statusDiscount == true" />
            <button @click="$emit('closeModal');">닫기</button>
        </div>
    </div>
</template>

<script>
import Discount from './Discount.vue';
export default {
    name : "ModalWindow",
    data(){
        return {
            month : 3,
            statusDiscount : Boolean,
        }
    },
    props : {
        roomsInfo : Array,
        currentProductNo : Number,
        modalStatus : Boolean,
        신고수 : Array,
        
    },
    components : {
        Discount,
    },    watch : {
        month(val){
            if(val >= 13){
                alert('13이상 입력하면 안됩니다');
                this.month = 3;
            } else if((val % 1) != 0){
                alert('정수만 입력하세요');
                this.month = 3;
            }
            if(isNaN(parseInt(val))){
                alert('숫자만 입력하세요');
                this.month = 3;
            }
        }
    },
    mounted(){
        setTimeout(()=>{
         this.statusDiscount = false;
        }, 5000);
    },
    beforeUpdate(){
        if(this.month <= 2){
            alert('2개월 이하는 너무 적어요... 3개월부터..');
            this.month = 3;
        }
    }
}
</script>

