  <div class="menu">
    <a v-for="menu in menus" :key="menu" @click="sale = menu">{{ menu }}</a>
  </div>

 
  <!-- <div class="discount" v-if="sale == 'Event'">
    <h4>지금 결제하면 20% 할인</h4>
  </div> -->
  
  <transition name="event">
    <Discount v-if="sale == 'Event'"/>
  </transition>


  <button @click="sortPriceAsc">가격 오름차순 정렬</button> &nbsp;
  <button @click="sortPriceDesc">가격 내림차순 정렬</button> &nbsp;
  <button @click="sortTitleAsc">제목 오름차순 정렬</button> &nbsp;
  <button @click="sortTitleDesc">제목 내림차순 정렬</button> &nbsp;
  <button @click="sortOrigin">초기화</button> &nbsp;



  <!-- <div class="aniStart" :class="{ aniEnd : modalStatus }">
    <Modal @초기화="신고수[$event]=0" @closeModal="modalStatus=false;" :roomsInfo="roomsInfo" :currentProductNo="currentProductNo"
    :modalStatus="modalStatus" :신고수="신고수" />
  </div> -->


  <transition name="fade">
    <Modal @초기화="신고수[$event]=0" @closeModal="modalStatus=false;" :roomsInfo="roomsInfo" :currentProductNo="currentProductNo"
    :modalStatus="modalStatus" :신고수="신고수"  />
  </transition>


  <Card @openModal="modalStatus=true; currentProductNo=$event" 
      :modalStatus="modalStatus"
      :신고수="신고수" @신고="신고수[$event]++" :room="room"
      v-for="room in roomsInfo" :key="room"  />

  <!-- <div class="black-bg" v-if="modalStatus == true">
