</template>

<script>
import data from "./assets/oneroom.js";
import Discount from "./components/Discount.vue"
import Modal from "./components/Modal.vue"
import Card from "./components/Card.vue"

export default {
  name: 'App',
  data(){
    return {
      // statusDiscount : true,
      sale : '',
      modalStatus : false,
      currentProductNo : 0,
      origin : [...data],
      roomsInfo : data,
      price : [50, 60, 70],
      products : ['역삼동원룸', '천호동원룸', '마포구원룸'],
      menus : ['Home', 'Shop', 'Event'],
      신고수 : [0, 0, 0, 0, 0, 0],
    }
  },
  components: {
    Discount : Discount,
    Modal : Modal,    
    Card : Card,
  
  },
  methods : {
    sortTest(){
    let array = [3, 5, 2];
    array.sort();
    console.log('오름차순 정렬 : ' + array);

    array.sort(function(a,b){
      return a-b;
    })
    console.log('내림차순 정렬 : ' + array);
    },

    // 숫자 정렬

    sortPriceAsc(){
      this.roomsInfo.sort(function(a,b){
        return a.price - b.price;
      });
    },
    sortPriceDesc(){
      this.roomsInfo.sort(function(a,b){
        return b.price - a.price;
      });
    },
    sortOrigin(){
      this.roomsInfo = [...this.origin];
    },
    sortTitleAsc(){
      this.roomsInfo.sort(function(a,b){
        if(a.title < b.title) return -1;
        if(a.title > b.title) return 1;
        else return 0;
      })
    },
    sortTitleDesc(){
      this.roomsInfo.sort((a,b)=>{
        if(a.title < b.title) return 1;
        if(a.title > b.title) return -1;
        else return 0;
      })
    }
  }
}
</script>

